import { useEffect, useState } from "react";
import { AiOutlinePlus, AiFillCloseSquare } from "react-icons/ai";
import { GoAlert } from "react-icons/go";
import { getDatabase, ref, set, onValue } from "firebase/database";
import { v1 as uuidv1 } from 'uuid';
function Admin() {
    const [toggle, setToggle] = useState(false);
    return <div>
        <Header openHandler={() => setToggle(true)} />
        <Modal toggle={toggle} closeHandler={() => setToggle(false)} />
        <View />
    </div>
}
export default Admin;

const Header = ({ openHandler }) => {
    return <header className="text-3xl flex justify-between shadow p-4">
        <div>Admin Panel</div>
        <AiOutlinePlus onClick={openHandler} className="cursor-pointer" />
    </header>
}

const Modal = ({ toggle, closeHandler }) => {
    const [error, setError] = useState("");
    const options = [
        'a', 'b', 'c'
    ]
    const submitHandler = (event) => {
        event.preventDefault();
        if (event.target.checkValidity()) {
            const data = {
                question: event.target.question.value,
                correct: event.target.correct.value,
                a: event.target.a.value,
                b: event.target.b.value,
                c: event.target.c.value
            }
            const quizId = uuidv1();
            const db = getDatabase();
            set(
                ref(db, `quizzes/${quizId}`), data)
                .then(
                    () => {
                        event.target.reset();
                    }
                ).catch(
                    (error) => {
                        setError("Unable to add data");
                    }
                )
        } else {
            setError("Please fill all the fields");
        }

    }


    return <div className={`top-0 fixed duration-700 flex flex-col ${toggle ? 'opacity-1 visible' : 'opacity-0 invisible'} justify-center items-center w-[100%] z-50 h-[100%] bg-[rgba(0,0,0,0.6)]`}>
        <div className="absolute top-4 right-4 text-white" >
            <AiFillCloseSquare className="text-4xl" onClick={closeHandler} />
        </div>
        <div className="w-[450px] rounded bg-white shadow min-h-[450px] p-3">
            <h4 className="text-2xl text-center">Add Quiz</h4>

            {
                error != "" && <div className="flex gap-2 justify-center items-center mt-3">
                    <span> {error} </span>
                    <GoAlert className="text-red-500" />
                </div>
            }
            <form action="" onSubmit={submitHandler} noValidate>
                <div className="flex flex-col gap-2 mt-2">
                    <label htmlFor="">Question</label>
                    <textarea required className="border focus:outline-none p-2 rounded" name="question" id=""></textarea>
                </div>
                {
                    options.map(
                        (d, i) => {
                            return <div className="flex flex-col gap-2 mt-2" key={i}>
                                <label htmlFor="">Option {i + 1} ({d.toUpperCase()})</label>
                                <input required type="text" name={d} id="" className="border focus:outline-none p-2 rounded" />
                            </div>
                        }
                    )
                }
                <div className="flex flex-col gap-2 mt-2">
                    <label htmlFor="">Correct Option</label>
                    <select required name="correct" id="" className="border focus:outline-none p-2 rounded" >
                        <option value="">Select here</option>
                        {
                            options.map(
                                (d, i) => {
                                    return <option key={i} value={d}>{d.toUpperCase()}</option>
                                }
                            )
                        }
                    </select>
                </div>
                <button className="border my-2 p-3 rounded">Save</button>
            </form>
        </div >
    </div >
}

const View = () => {
    const [quizzes, setQuizzes] = useState([]);
    useEffect(
        () => {
            const db = getDatabase();
            const starCountRef = ref(db, 'quizzes');
            onValue(starCountRef, (snapshot) => {
                const data = snapshot.val();
                const arr = [];
                Object.keys(data).forEach(
                    (key) => {
                        arr.push({ ...data[key], id: key });
                    }
                )
                setQuizzes(arr);
            });
        },
        []
    );
    console.log(quizzes);
    const boxes = quizzes.map(
        (quiz, index) => {
            return (
                <div className="shadow p-3 mt-3 rounded" key={index}>
                    <div className="text-2xl">{index + 1}) {quiz.question}</div>
                    <ul className="pl-5 text-xl">
                       <li className={`${quiz.correct == "a" ? 'font-bold' : ''}`}>A) {quiz.a}</li>
                       <li className={`${quiz.correct == "b" ? 'font-bold' : ''}`}>B) {quiz.b}</li>
                       <li className={`${quiz.correct == "c" ? 'font-bold' : ''}`}>C) {quiz.c}</li>
                    </ul>
                </div>
            )
        }
    )
    return <div className="max-w-[1240px] mx-auto">
        {boxes}
        {/* <div className="shadow p-3 mt-3 rounded">
            <div className="text-2xl">1) This is the question</div>
            <ul className="pl-5 text-xl">
                <li>a) Option1</li>
                <li className="font-bold">b) Option1</li>
                <li>c) Option1</li>
            </ul>
        </div> */}
    </div>
}